  font-size: 13px;
  line-height: 20px;
  box-shadow: inset 0 0 5px #000;
  word-wrap: break-word;
  color: #d6d6d6;
  background-color: #282c34;
  padding: 0;
}

pre, pre.lines {
  margin: 0;
  padding-left: 0;
  padding-right: 0;
}

pre.lines {
