  box-shadow: none;
  color: #d6d6d6;
  background-color: #000;
  width: 40px;
  padding-top: 7px;
  padding-bottom: 7px;
  text-align: center !important;
}

pre code {
  padding: 10px 5px;
  font-size: 1em;
}

code {
  max-width: 100%;
}

p code, li code, h1 code, h2 code, h3 code, h4 code, h5 code {
  background-color: #c5c7cc;
  padding: 3px 4px;
  font-size: .85em;
}

